            @import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,500;0,600;1,400&display=swap');
            $darkgreen: #0D6830;
            $green:#15A14A;
            $text: #404040;
            *,
            *:before,
            *:after {
                padding: 0;
                margin: 0;
                box-sizing: border-box;
                font-family: 'Nunito', sans-serif;
            }
            
            html,
            body {
                background-color: #F6F7F8;
            }
            
            .container {
                max-width: 1200px;
                margin: 0 auto;
                padding: 0 15px;
            }
            
            .header {
                z-index: 65;
                position: fixed;
                width: 100%;
                background: transparent;
                top: 0;
                transition: 0.2s;
                padding-top: 10px;
                padding-bottom: 10px;
                &__container {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }
                &.active {
                    transition: 0.2s;
                    box-shadow: 0 0 5px 0 rgb(0 0 0 / 32%);
                    background-color: rgba(255, 255, 255, .98)
                }
                &__menu-burger {
                    display: none;
                }
                &__menu {
                    display: flex;
                }
                &__menu-item {
                    padding: 16px 3px;
                    padding-bottom: 10px;
                    font-size: 23px;
                    text-decoration: none;
                    color: #ffff;
                    transition: 0.2s;
                    border-bottom: 3px transparent solid;
                    &:hover {
                        color: $green;
                        border-bottom: 3px $green solid;
                    }
                    &:active {
                        color: $darkgreen;
                        border-bottom: 3px $darkgreen solid;
                    }
                }
                &__menu-item:first-child {
                    margin-right: 40px;
                    position: relative;
                    cursor: pointer;
                }
                &__menu-item:first-child::after {
                    content: "";
                    width: 7px;
                    height: 7px;
                    top: 28px;
                    border: 7px solid transparent;
                    border-top: 7px solid #fff;
                    position: absolute;
                    margin-left: 6px;
                    transform: rotate(180eg);
                    -webkit-transform: rotate(180eg);
                    -moz-transform: rotate(180eg);
                    -ms-transform: rotate(180eg);
                    -o-transform: rotate(180eg);
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                }
                &__menu-item:first-child:hover::after {
                    content: "";
                    position: absolute;
                    transform: rotate(180deg);
                    -webkit-transform: rotate(180deg);
                    -moz-transform: rotate(180deg);
                    -ms-transform: rotate(180deg);
                    -o-transform: rotate(180deg);
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    top: 20px;
                    border-top: 10px solid $green;
                }
                &__menu-item:first-child:hover {
                    border-bottom: 3px transparent solid;
                }
                &__menu-dropdown {
                    display: none;
                    position: absolute;
                    top: 55px;
                    flex-direction: column;
                    transition: 0.2s;
                    background: transparent;
                    border-radius: 5px;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                }
                &__dropdown-item {
                    display: flex;
                    align-items: center;
                    padding: 10px;
                    color: #fff;
                    font-size: 19px;
                    text-decoration: none;
                    padding-right: 20px;
                    white-space: nowrap;
                    padding-right: 29px;
                    padding-left: 0;
                    border-bottom: 3px transparent solid;
                    width: fit-content;
                }
                &__dropdown-item:hover {
                    color: $green;
                    border-bottom: 3px $green solid;
                }
                &__dropdown-item:active {
                    color: $darkgreen;
                    border-bottom: 3px $darkgreen solid;
                }
            }
            
            .drop__wrap {
                position: relative;
                display: flex;
                justify-content: flex-start;
                align-items: center;
            }
            
            .drop__wrap:hover .header__menu-dropdown {
                display: flex;
                // top: 66px;
                transition: 0.3s;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -ms-transition: 0.3s;
                -o-transition: 0.3s;
            }
            
            .header.active .header__menu-dropdown {
                background-color: rgba(255, 255, 255, .98);
                box-shadow: 0 0 5px 0 rgb(0 0 0 / 32%);
            }
            
            .header.active .header__menu-item:first-child:hover {
                border-bottom: 3px transparent solid;
            }
            
            .header.active .header__menu-item:first-child::after {
                border-top: 7px solid #252525;
            }
            
            .header.active .header__menu-item:first-child:hover::after {
                border-top: 7px solid $green;
            }
            
            .header.active .header__dropdown-item {
                color: $green;
                padding-left: 10px;
                padding-right: 10px;
            }
            
            .header.active .header__dropdown-item:last-child {
                margin-bottom: 10px;
            }
            
            .header__logo-wrap {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 450px;
                transition: 1s;
                -webkit-transition: 1s;
                -moz-transition: 1s;
                -ms-transition: 1s;
                -o-transition: 1s;
            }
            
            .header__logo-1 {
                width: 22%;
                height: 100%;
                margin-right: 10px;
            }
            
            .header__logo-2 {
                width: 70%;
                height: 100%;
                transition: 0.2s;
                color: #ffff;
                font-size: 16px;
            }
            
            .header__logo-wrap-reke {
                display: none;
            }
            
            .header.active .header__logo-wrap-reke {
                display: flex;
            }
            
            .header.active .header__menu-item {
                color: #404040;
                transition: 0.2s;
                &:hover {
                    color: $green;
                    border-bottom: 3px $green solid;
                }
                &:active {
                    color: $darkgreen;
                    border-bottom: 3px $darkgreen solid;
                }
            }
            
            .header.active .header__logo-wrap {
                // width: 200px;
                transition: 1s;
                -webkit-transition: 1s;
                -moz-transition: 1s;
                -ms-transition: 1s;
                -o-transition: 1s;
            }
            
            .header.active .header__logo-1 {
                transition: 0.3s;
                width: 13%;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -ms-transition: 0.3s;
                -o-transition: 0.3s;
            }
            
            .header.active .header__logo-2 {
                //display: none;
                transition: 0.2s;
                font-size: 13px;
                color: #252525;
                transition: 0.3s;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -ms-transition: 0.3s;
                -o-transition: 0.3s;
            }
            
            .header.active .header__menu-item {
                font-size: 20px;
                transition: 0.2s;
            }
            
            .footer {
                padding: 20px 0;
                background-color: #252525;
                margin-top: 100px;
                &__container {
                    display: flex;
                    justify-content: space-between;
                }
                &__title {
                    color: $green;
                    text-transform: uppercase;
                    font-weight: 600;
                    font-size: 22px;
                    text-decoration: none;
                    width: 30%;
                    &:hover {
                        color: darkgreen;
                    }
                    &:active {
                        color: $darkgreen;
                    }
                }
                &__contact {
                    display: flex;
                    flex-direction: column;
                }
                .contact__title {
                    color: $green;
                    font-weight: 600;
                    font-size: 22px;
                    border-bottom: 5px $green solid;
                    border-radius: 5px;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                    width: fit-content;
                    margin-bottom: 25px;
                }
                .contact__line {
                    color: #ffff;
                    text-decoration: none;
                    margin-bottom: 5px;
                }
                a.contact__line:hover {
                    color: $green;
                }
                a.contact__line:active {
                    color: $darkgreen;
                }
            }
            
            .director {
                background: url(../assets/fon.jpg) center no-repeat;
                background-color: #15A14A;
                background-size: cover;
                position: relative;
                &:before {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background-color: rgba(0, 0, 0, 0.401);
                }
                &__container {
                    z-index: 55;
                    padding-top: 180px;
                    padding-bottom: 100px;
                    display: flex;
                    z-index: 55;
                    flex-direction: column;
                }
                &__index {
                    z-index: 55;
                    padding-top: 180px;
                    padding-bottom: 100px;
                    display: flex;
                    align-items: center;
                    z-index: 55;
                    flex-direction: row;
                }
                &__title {
                    color: #ffff;
                    font-size: 30px;
                    font-weight: 600;
                    z-index: 55;
                    -webkit-touch-callout: none;
                    -webkit-user-select: none;
                    -khtml-user-select: none;
                    -moz-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
                }
                &__name {
                    font-size: 55px;
                    color: #ffff;
                    font-weight: 600;
                    z-index: 55;
                }
                &__logo {
                    width: 200px;
                    z-index: 1;
                    margin-right: 30px;
                }
                &__logo-img {
                    width: 100%;
                    height: 100%;
                }
            }
            
            .director__text {
                border-left: 1px solid $darkgreen;
                padding: 30px 15px;
                padding-top: 0;
                padding-bottom: 0;
                border-right: 1px solid $darkgreen;
                margin-top: 20px;
            }
            
            .news {
                margin-top: 100px;
                &__title {
                    color: $green;
                    font-size: 30px;
                    font-weight: 600;
                    border-top: solid $green 5px;
                    width: fit-content;
                    padding-top: 20px;
                    margin-bottom: 50px;
                }
                &__item {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-bottom: 30px;
                }
                &__foto:first-child {
                    margin-right: 40px;
                }
                &__foto {
                    width: 30%;
                    border-radius: 5px;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                }
                &__img {
                    width: 100%;
                    border-radius: 5px;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                }
                &__text {
                    width: 70%;
                }
            }
            
            .about {
                margin-top: 100px;
                &__title {
                    color: $green;
                    font-size: 30px;
                    font-weight: 600;
                    border-top: solid $green 5px;
                    width: fit-content;
                    padding-top: 20px;
                    margin-bottom: 50px;
                }
                &__btns {
                    display: flex;
                    flex-direction: column;
                }
                &__btn {
                    background: #fff;
                    border: 1px solid rgba(0, 0, 0, .1);
                    color: $green;
                    border-radius: 30px;
                    position: relative;
                    padding: 1.8rem 5rem 1.8rem 2.2rem;
                    margin-bottom: 25px;
                    font-size: 28px;
                    text-decoration: none;
                    &:hover {
                        border: 1px solid $green;
                    }
                    &:active {
                        border: 1px solid $darkgreen;
                        color: $darkgreen;
                    }
                }
                &__btn::after {
                    content: '';
                    position: absolute;
                    background: url(../assets/button-arrow.svg) center no-repeat;
                    width: 69px;
                    height: 69px;
                    right: 45px;
                    top: 15%;
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    border-radius: 25%;
                    -webkit-border-radius: 25%;
                    -moz-border-radius: 25%;
                    -ms-border-radius: 25%;
                    -o-border-radius: 25%;
                    background-color: #ecf5ef;
                }
                &__btn:hover::after {
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    right: 15px;
                    cursor: pointer;
                }
            }
            
